@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumBarunGothic@latest/nanumbarungothicsubset.css);
body {
  margin: 0;
  font-family: 'NanumBarunGothic', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: #444444;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F8F8F8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;
  padding-top: 100px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.desktop-menu{background-color:#273339;position:fixed;top:0;width:100%;-webkit-transition:top 0.3s;transition:top 0.3s;padding-left:50px}.desktop-menu a{float:left;display:block;color:white;text-align:center;padding:15px;text-decoration:none;font-weight:bold;font-size:20px}.desktop-menu a:hover{background-color:#F8F8F8;color:#273339}

.who{margin-bottom:50px}.who img{width:250px}.who .my-info{padding-top:50px;text-align:left;padding-left:50px;font-size:20px}.who .my-info .nickname{font-size:30px;font-weight:bold}.who .my-info .intro{color:#F1A2A6;padding-top:10px;font-size:15px}.who .url-card{background-color:#273339;color:#F3F7F6;font-size:20px;border-radius:10px;display:inline-block;text-decoration:none;padding:10px 20px;margin:20px 10px 0 0}.who .url-card:hover{background-color:#F1A2A6}.who .legacy-stack-text{padding-top:40px;font-size:20px;font-weight:bold}.who .legacy-stack-exof{text-decoration:none;display:inline-block;color:white;background-color:#3889ED;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-sqlite{text-decoration:none;display:inline-block;color:white;background-color:#3B9C8F;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-etree{text-decoration:none;display:inline-block;color:white;background-color:#E93B35;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-jwt{text-decoration:none;display:inline-block;color:white;background-color:#122736;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-queue{text-decoration:none;display:inline-block;color:white;background-color:#783C3D;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-cell{text-decoration:none;display:inline-block;color:white;background-color:gray;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-next{text-decoration:none;display:inline-block;color:white;background-color:#000;width:100%;padding:10px 0;margin-top:5px}

.contents-banner{text-align:left;position:relative;background-color:#f5f1f0;margin-bottom:10px}.contents-banner a{text-decoration:none;color:#363433}.contents-banner .banner{padding:1.5em 1.5em}.contents-banner .effect ::after{content:'';display:block;position:absolute;width:10%;height:40%;border:2px solid;-webkit-transition:all 0.6s ease;transition:all 0.6s ease;border-radius:2px;bottom:0;right:0;border-top-color:transparent;border-left-color:transparent;border-bottom-color:#363433;border-right-color:#363433}.contents-banner .effect ::before{content:'';display:block;position:absolute;width:10%;height:40%;border:2px solid;-webkit-transition:all 0.6s ease;transition:all 0.6s ease;border-radius:2px;top:0;left:0;border-bottom-color:transparent;border-right-color:transparent;border-top-color:#363433;border-left-color:#363433}.contents-banner .effect :hover:after{width:100%;height:100%}.contents-banner .effect :hover:before{width:100%;height:100%}.contents-banner .subject{font-weight:bold;font-size:25px}.contents-banner .description{font-size:15px}

.page-number-back{padding-top:40px}.page-number-back .page-number{font-size:20px;padding:10px 10px;color:#282c34;font-weight:bold}

.contents{text-align:right}.contents a{font-size:18px}.contents a:hover{cursor:pointer}

