$main-color: #363433;

.contents-banner {
  text-align: left;
  position: relative;
  background-color: #f5f1f0;
  margin-bottom: 10px;

  a {
    text-decoration: none;
    color: $main-color;
  }

  .banner {
    padding: 1.5em 1.5em;
  }

  .effect {
    @mixin ab {
      content: '';
      display: block;
      position: absolute;
      width: 10%;
      height: 40%;
      border: 2px solid;
      transition: all 0.6s ease;
      border-radius: 2px;
    }

    ::after {
      @include ab;

      bottom: 0;
      right: 0;
      border-top-color: transparent;
      border-left-color: transparent;
      border-bottom-color: $main-color;
      border-right-color: $main-color;
    }
    ::before {
      @include ab;

      top: 0;
      left: 0;
      border-bottom-color: transparent;
      border-right-color: transparent;
      border-top-color: $main-color;
      border-left-color: $main-color;
    }

    @mixin hab {
      width: 100%;
      height: 100%;
    }

    :hover:after {
      @include hab;
    }
    :hover:before {
      @include hab;
    }
  }

  .subject {
    font-weight: bold;
    font-size: 25px;
  }

  .description {
    font-size: 15px;
  }
}